import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Tab} from './tab';
import { Observable ,of} from 'rxjs';

import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class TabsService {
 apiURL: string = 'https://cors-anywhere.herokuapp.com/http://cuhacking-2019-browserbuddies.appspot.com/'
  constructor(private httpClient:HttpClient) { }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
    alert(JSON.stringify(error)); // log to console instead

      // TODO: better job of transforming error for user consumption
      //this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
  public test(){
    alert("test")
  }
  public getTabs():Observable<any>{
  //  return
  //alert("getTabs clicked: "+JSON.stringify(this.httpClient.get<Tab[]>('${this.apiURL}/tablist')))
  //return this.httpClient.get
  return this.httpClient.get<Tab[]>(this.apiURL + "/tablist")
  .pipe(
     catchError(this.handleError('getTabs', []))
   );
  }

  public addTab(t: Tab):Observable<any>{
    alert("addTab")
    return this.httpClient.post("http://cuhacking-2019-browserbuddies.appspot.com"+"/addtabs",t)
  }
}
